@import url(https://fonts.googleapis.com/css2?family=Newsreader&display=swap);

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Newsreader', serif;
  background-color: #1a1a1c;
  color: #fff;
}

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff;
}
.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

.coin-text {
    margin-bottom: 32px;
    text-align: center;
}

.coin-input {
    padding-left: 16px;
    width: 300px;
    height: 50px;
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(
        -255deg,
        #ac32e4 0%,
        #7918f2 48%,
        #4801ff 100%
    );
    color: #e2e2e2
}

.coin-input::-webkit-input-placeholder {
    color: #e2e2e2
}

.coin-input:-ms-input-placeholder {
    color: #e2e2e2
}

.coin-input::placeholder {
    color: #e2e2e2
}

.dataTitle-main-container{
    width: 1000px;
    display: flex;
    justify-content: flex-end;
}
.dataTitle-container {
    width: 700px;
    display: flex;
    justify-content: space-around;
}

.dataTitle-title {
    font-size: 20px;
    font-weight: bold;
    padding-right: 50px;
}
.coin-container {
    display: flex;
    justify-content:center; 
}

.coin-row {
    display: flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
    width: 1000px;
}

.coin {
    display: flex;
    align-items: center;
    min-width: 300px;
}

.coin img {
    height: 30px;
    width: 30px;
    margin: 0 10px; 
}

.coin h1 {
    font-size: 20px;
    width: 150px;
    margin-right: 10px; 
}

.coin-symbol {
    text-transform: uppercase
}

.coin-data {
    display: flex;
    text-align: right;
    justify-content: space-between;
    width: 100%;
}

.data-center {
    display: flex;
    justify-content: center;
    align-items: center;
}
.coin-price {
    width: 110px;
    font-size: 22px;
}

.coin-volume {
    width: 155px;
    font-size: 16px;
}

.coin-marketcap {
    width: 230px;
    font-size: 16px;
}

.coin-percent {
    width: 100px;
    font-size: 20px;
    font-weight: bold;
}

.red {
    color: #f00606;
}

.green {
    color: #11d811;
}


